.slick-arrow {
	position: absolute;
	z-index: 10;
	border: none;
	-webkit-appearance: none;
	width: 41px;
	height: 41px;
	background: none;
	transition: all .2s ease;
	font-size: 0;
	line-height: 0;
	color: transparent;
	text-indent: -20000px;
	overflow: hidden;
	top: 50%;
	margin-top: -20px;
	cursor: pointer;
	outline: none !important;
}

.slick-arrow:before {
	content: "";
	background-repeat: no-repeat;
	background-position: center center;
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	margin: 0;
}

.slick-prev:before {
	background-image: url(../images/slick-prev.svg);
}

.slick-next:before {
	background-image: url(../images/slick-next.svg);
}

.slick-prev {
	left: 15px;
}

.slick-next {
	right: 15px;
}

