.top-slider .slick-list {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	overflow: visible;
}

.top-slider .slick-track {
	min-height: 100%;
	display: flex;
	overflow: visible;
}

.top-slider .slide {
	height: auto;
	float: none;
	position: relative;
}

.top-slider .slide:after {
	content: "";
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	transition: all .75s ease;
	background: #EE6700;
	opacity: .7;
}

.top-slider .slide.slick-active:after {
	opacity: 0;
}

.top-slider .slide-pic {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
}