.form-group {
	position: relative;
	margin: 0 0 34px 0;
	padding: 22px 0 0 0;
}

label.placeholder {
	font-family: 'Body Text Fit';
	font-size: 18px;
	line-height: 1.6;
	font-weight: normal;
	color: #fff;
	display: block;
	position: absolute;
	top: 22px;
	transition: all .3s ease;
	padding: 0;
	margin: 0;
}

label.placeholder.active {
	font-size: 14px;
	line-height: 20px;
	top: 0;
}

label.error {
	position: absolute;
	font-size: 14px;
	line-height: 20px;
	color: #D13C3C;
	margin: 5px 0 0 0;
}

input[type=text],
input[type=email],
input[type=password],
textarea {
	border: none;
	background: none;
	border-bottom: 1px solid #fff;
	transition: all .2s ease;
	font-family: 'Body Text Fit';
	font-size: 18px;
	line-height: 1.6;
	font-weight: normal;
	color: #fff;
	padding: 0 0 12px 0;
	width: 100%;
	display: block;
	outline: none !important;
}

textarea {
	resize: none;
}

input[type=text].error,
input[type=email].error,
input[type=password].error,
textarea.error {
	border-bottom-color: #D13C3C;
	color: #D13C3C;
}

.form-footer {
	margin: 16px 0 0 0;
}

.feedback-form .form-footer .btn {
	min-width: 285px;
}

.form-col-name {
	flex: 0 0 58.4%;
	max-width: 58.4%;
}

.form-col-phone {
	flex: 0 0 41.6%;
	max-width: 41.6%;
}

@media (max-width: 767px) {

	.form-group {
		margin: 0 0 30px 0;
	}

	label.placeholder {
		font-size: 15px;
		line-height: 20px;
	}

	label.placeholder.active {
		font-size: 12px;
		line-height: 15px;
	}

	input[type=text],
	input[type=email],
	input[type=password],
	textarea {
		font-size: 15px;
		line-height: 20px;
	}

}