#md-indicator {
	display: none;
}

#sm-indicator {
	display: none;
}

#mobile-indicator {
	display: none;
}

.navbar-trigger {
	display: none;
}

.navbar-wrapper .lang-selector {
	display: none;
}

@media (min-width: 768px) {

	.navbar-wrapper {
		display: block !important;
		opacity: 1 !important;
	}

}

// lg

@media (max-width: 1329px) {

	header {
		font-size: 14px;
	}

	.lang-selector {
		margin: 0;
	}

	.header-phone {
		margin: 0;
		font-size: 14px;
	}

	.btn-callback,
	.btn-callback:focus {
		font-size: 14px;
	}

	ul.navbar-nav {
		margin-left: -10px;
		margin-right: -10px;
	}

	ul.navbar-nav > li {
		margin-left: 10px;
		margin-right: 10px;
	}

	.service-tmb.active .service-tmb-content {
		padding: 100px 70px 100px 100px;
	}

	.section-header h2, .section-header .h2 {
		font-size: 55px;
	}

	.date-tmb {
		font-size: 50px;
	}

	.slick-active .date-tmb {
		font-size: 75px;
	}

	.col-history-l {
		flex: 0 0 28%;
		max-width: 28%;
	}

	.history-tmb-descr {
		flex: 0 0 45%;
		max-width: 45%;
	}

	.history-tmb-pic {
		flex: 0 0 55%;
		max-width: 55%;
	}

	.project-tmb-descr {
		padding: 37px 30px 46px 40px;
	}

	.project-tmb-descr h3, .project-tmb-descr .h3 {
		font-size: 28px;
	}

	.contacts-address {
		font-size: 40px;
	}

	.geo:before {
		background-size: 100% auto;
		width: 500px;
	}

}

@media (max-width: 1169px) {

	#md-indicator {
		display: block;
	}

	html,
	body {
		font-size: 16px;
	}

	.section {
		min-height: 0 !important;
	}

	.header-col-3 {
		order: 1;
		flex: 0 0 100%;
		max-width: 100%;
	}

	.header-col-2 {
		order: 2;
	}

	.header-col-4 {
		order: 4;
	}

	.header-col-group-r > .row {
		justify-content: flex-end;
	}

	ul.navbar-nav {
		justify-content: flex-end;
		margin-left: -15px;
		margin-right: -15px;
	}

	ul.navbar-nav > li {
		margin-left: 15px;
		margin-right: 15px;
	}

	.navbar-wrapper {
		margin-bottom: 15px;
	}

	.services-list {
		display: block;
	}

	.service-tmb {
		width: auto !important;
		max-height: 250px;
		overflow: hidden;
	}

	.service-tmb-ttl {
		transform: none !important;
		bottom: auto;
		top: 50%;
		margin-top: -15px;
	}

	.service-tmb-content {
		padding: 100px 70px 100px 100px;
	}

	.service-tmb-arrow {
		bottom: auto;
		top: 50%;
		right: 50px;
		margin-top: -20px;
	}

	.section-header h2, .section-header .h2 {
		font-size: 40px;
	}

	.section-about-text {
		margin: 0;
	}

	.about-descr {
		padding: 0 20px 0 0;
	}

	.about-stats-tmb-num {
		line-height: 45px;
	}

	.about-stats-tmb-num span.num {
		font-size: 45px;
	}

	.about-stats-tmb-descr {
		margin: 0 0 0 40px;
	}

	.about-stats-tmb + .about-stats-tmb {
		margin-left: 40px;
	}

	.geo:before {
		width: 410px;
	}

	.geo {
		padding: 0 0 0 20px;
		//position: relative;
	}

	.geo-tmb {
		font-size: 36px;
	}

	.history-tmb {
		flex-wrap: wrap;
	}

	.history-tmb-descr {
		flex: 0 0 100%;
		max-width: 100%;
		order: 2;
	}

	.history-tmb-pic {
		flex: 0 0 100%;
		max-width: 100%;
		order: 1;
		margin-bottom: 50px;
	}

	.col-history-l {
		flex: 0 0 40%;
		max-width: 40%;
	}

	.col-history-r {
		flex: 0 0 60%;
		max-width: 60%;
	}

	.col-projects-l {
		flex: 0 0 100%;
		max-width: 100%;
	}

	.col-projects-r {
		flex: 0 0 100%;
		max-width: 100%;
	}

	.project-tmb {
		padding-bottom: 15px;
	}

	.projects-nav {
		margin: 0 175px 38px -15px;
		padding: 0;
		max-width: initial;
	}

	.projects-nav:after {
		display: none;
	}

	.projects-nav-item {
		flex-grow: 0;
		flex-shrink: 1;
		flex-basis: auto;
		max-width: initial;
		margin: 0 15px;
	}

	.projects-nav-item .cont {
		position: relative;
		left: auto;
		right: auto;
		top: auto;
		bottom: auto;
		transform: none;
	}

	.contacts-map {
		width: 400px;
		height: 400px;
	}

	.project-modal .close {
		right: 20px;
		top: 20px;
	}

	.project-item-header h2, .project-item-header .h2 {
		font-size: 50px;
	}

	.form-modal-header h2, .form-modal-header .h2 {
		font-size: 50px;
	}

}

// lg END

// md

@media (max-width: 991px) {

	#sm-indicator {
		display: block;
	}

	.section-top-header h1, .section-top-header .h1 {
		font-size: 50px;
	}

	.service-tmb {
		max-height: 200px;
	}

	.section-about:before {
		display: none;
	}

	.col-about-1 {
		flex: 0 0 100%;
		max-width: 100%;
	}

	.about-descr {
		padding: 0;
	}

	.col-about-2 {
		flex: 0 0 100%;
		max-width: 100%;
	}

	.section-header h2, .section-header .h2 {
		font-size: 36px;
	}

	.geo {
		margin-top: 80px;
		padding: 0;
	}

	.col-history-l {
		flex: 0 0 100%;
		max-width: 100%;
	}

	.col-history-r {
		flex: 0 0 100%;
		max-width: 100%;
	}

	.date-tmb {
		font-size: 20px;
		line-height: 30px;
		height: 40px;
		-ms-flex-direction: row;
		flex-direction: row;
		-ms-flex-pack: center;
		justify-content: center;
		-ms-flex-align: center;
		align-items: center;
		width: 110px;
	}

	.slick-active .date-tmb {
		font-size: 50px;
		-webkit-transform: translateY(47px);
		transform: translateY(47px);
	}

	.dates-slider {
		width: 110px;
		margin: 0 auto;
	}

	.dates-slider-wrapper {
		height: auto;
		padding-top: 0px;
		width: 330px;
		margin: 0 auto;
		padding: 0 110px 50px 110px;
	}

	.dates-slider .slide {
		width: 100px;
		padding: 0;
	}

	.dates-slider .slick-prev {
		left: -110px;
		top: auto;
		bottom: -50px;
		margin: 0;
	}

	.dates-slider .slick-next {
		left: auto;
		right: -100px;
		top: auto;
		bottom: -50px;
		margin: 0;
	}

	.dates-slider-over-wrapper {
		padding: 0;
		width: 330px;
		margin: 0 auto 35px auto;
	}

	.dates-slider-over-wrapper:before {
		display: none;
	}

	.dates-slider-over-wrapper .slick-prev {
		top: auto;
		left: 0;
		bottom: 0;
	}

	.dates-slider-over-wrapper .slick-next {
		left: auto;
		top: auto;
		right: 0;
		bottom: 0;
	}

	.project-tmb {
		flex-wrap: wrap;
	}

	.project-tmb-pic {
		margin: 0;
		flex: 0 0 100%;
		max-width: 100%;
	}

	.project-tmb-descr {
		margin: 0;
		flex: 0 0 100%;
		max-width: 100%;
		padding: 0;
	}

	.project-tmb-pic:before {
		display: none;
	}

	.project-tmb-descr:before {
		display: none;
	}

	.projects-nav {
		margin-left: -10px;
		margin-bottom: 40px;
	}

	.projects-nav-item {
		font-size: 14px;
		margin: 0 10px;
	}

	.more-link-wrapper {
		margin: 40px 0 0 0;
	}

	.contacts-address {
		font-size: 30px;
	}

	.contacts-phone-wrapper {
		font-size: 20px;
	}

	.contacts-phone:before {
		top: -7px;
	}

	.project-item-header h2, .project-item-header .h2 {
		font-size: 40px;
	}

	.form-modal-header h2, .form-modal-header .h2 {
		font-size: 40px;
	}

	.project-item h3, .project-item .h3 {
		font-size: 30px;
	}

	.project-modal .modal-dialog {
		padding: 50px 75px;
	}

	.project-item-info {
		font-size: 16px;
	}

	.project-item-date {
		padding: 20px 30px 0 0;
	}

	.project-item-cat {
		padding: 20px 0 0 30px;
	}

}

// md END

// sm

@media (max-width: 767px) {

	#mobile-indicator {
		display: block;
	}

	.menu-open {
		overflow: hidden;
	}

	.navbar-wrapper {
		display: none;
		margin: 0;
		position: fixed;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background: #EE6700;
		padding: 40px 60px 40px 40px;
	}

	.header-content {
		padding: 10px 0 10px 0;
	}

	.header-logo img {
		width: 130px;
	}

	.header-col-4 {
		display: none;
	}

	.header-col-2 {
		order: 1;
	}

	.header-col-3 {
		order: 2;
		flex-grow: 0;
		flex-shrink: 1;
		flex-basis: auto;
	}
	
	.header-col-1 {
		flex: 0 0 190px;
		max-width: 190px;
	}

	.navbar-trigger {
		width: 30px;
		height: 30px;
		position: relative;
		z-index: 550;
		cursor: pointer;
		display: block;
	}

	.navbar-trigger span {
		height: 2px;
		background: #fff;
		position: absolute;
		left: 0;
		right: 0;
		transition: all .3s ease;
	}

	.navbar-trigger span:nth-child(1) {
		top: 2px;
	}

	.navbar-trigger span:nth-child(2) {
		top: 50%;
		margin-top: -1px;
	}

	.navbar-trigger span:nth-child(3) {
		bottom: 2px;
	}

	.navbar-trigger.active span:nth-child(1) {
		top: 50%;
		margin-top: -1px;
		transform: rotate(45deg);
	}

	.navbar-trigger.active span:nth-child(2) {
		opacity: 0;
	}

	.navbar-trigger.active span:nth-child(3) {
		bottom: 50%;
		margin-bottom: -1px;
		transform: rotate(-45deg);
	}

	ul.navbar-nav {
		font-size: 20px;
		display: block;
		margin: 0;
	}

	ul.navbar-nav > li {
		margin: 0 0 30px 0;
	}

	.navbar-contacts {
		display: block;
		margin: 50px 0 0 0;
	}

	.navbar-contacts .header-phone {
		font-size: 20px;
		margin-bottom: 30px;
	}

	.btn-callback,
	.btn-callback:focus {
		font-size: 20px;
		padding: 0 0 0 30px;
	}

	.btn-callback:after {
		right: auto;
		left: 0;
	}

	.section-top-descr {
		flex-wrap: wrap;
	}

	.section-top-header {
		flex: 0 0 100%;
		max-width: 100%;
	}

	.section-top-text {
		flex: 0 0 100%;
		max-width: 100%;
		border: none;
		margin: 0;
		padding: 0;
	}

	.service-tmb-ttl {
		margin-top: -9px;
	}

	.section-top-header h1, .section-top-header .h1 {
		font-size: 30px;
		margin: 0 0 20px 0;
	}

	.service-tmb-ttl h3, .service-tmb-ttl .h3 {
		font-size: 20px;
		margin: 0;
	}

	.service-tmb-ttl:before {
		font-size: 20px;
		margin-right: 10px;
	}

	.service-tmb-arrow {
		width: 30px;
		height: 30px;
		right: 15px;
		bottom: 15px;
		top: auto;
		margin: 0;
	}

	.service-tmb-arrow:before {
		background-position: right bottom;
	}

	.service-tmb {
		max-height: 100px;
	}

	.service-tmb-bg:after {
		content: "";
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background: rgba(0,0,0,.4);
	}

	.service-tmb-ttl {
		left: 60px;
	}

	.service-tmb-header h3, .service-tmb-header .h3 {
		font-size: 20px;
	}

	.service-tmb-header:before {
		margin-right: 10px;
		font-size: 20px;
	}

	.service-tmb-content,
	.service-tmb.active .service-tmb-content {
		padding: 40px 20px 40px 60px;
	}

	.service-tmb-header {
		margin: 0 0 25px 0;
	}

	.section-about {
		padding: 60px 0 60px 0;
	}

	.section-header h2, .section-header .h2 {
		font-size: 30px;
	}

	.section-about-pic {
		margin: 40px 0 40px 0;
	}

	about-stats-tmb-num {
		line-height: 35px;
	}

	.about-stats-tmb-num span.num {
		font-size: 35px;
	}

	.geo-tmb {
		font-size: 30px;
	}

	.geo-tmb-descr {
		padding-top: 3px;
	}

	.section-history {
		padding: 60px 0 60px 0;
	}

	.projects-nav {
		margin: 0 -20px;
		padding: 0 10px 20px 10px;
		overflow: auto;
	}

	.section-projects {
		padding: 60px 0 60px 0;
	}

	.projects-slider > .slick-prev,
	.projects-slider > .slick-next {
		top: -30px;
	}

	.projects-slider-wrapper {
		margin-top: 50px;
	}

	.project-tmb-descr h3, .project-tmb-descr .h3 {
		font-size: 24px;
	}

	.contacts-col-1 {
		flex: 0 0 100%;
		max-width: 100%;
	}

	.contacts-col-2 {
		flex: 0 0 100%;
		max-width: 100%;
	}

	.contacts-map {
		width: 300px;
		height: 300px;
		margin: 0 auto;
	}

	.contacts-descr {
		margin-bottom: 60px;
	}

	.footer-dev {
		margin: 0;
	}

	.footer-soclinks {
		margin: 0 -15px;
	}

	footer {
		font-size: 10px;
		line-height: 13px;
	}

	.project-modal .close,
	.form-modal .close {
		transform: scale(.75);
		transform-origin: right top;
		top: 10px;
		right: 10px;
	}

	.project-modal .modal-dialog,
	.form-modal .modal-dialog {
		padding: 60px 15px 30px 15px;
	}

	.project-item-header h2, .project-item-header .h2 {
		font-size: 26px;
	}

	.form-modal-header h2, .form-modal-header .h2 {
		font-size: 30px;
	}

	.project-item h3, .project-item .h3 {
		font-size: 20px;
	}

	.project-item-info {
		margin: 30px 0 0 0;
		font-size: 14px;
	}

	.project-item-date {
		padding: 10px 15px 0 0;
	}

	.project-item-cat {
		padding: 10px 0 0 15px;
	}

	.modal-form .col {
		flex: 0 0 100%;
		max-width: 100%;
	}

	.btn-submit, .btn-submit:focus {
		font-size: 20px;
	}

	.btn-submit span:after {
		top: -2px;
	}

	.top-slider {
		right: 0;
	}

}


// sm END
